body {
  background-color: #111;
}

.message {
  color: #fff;
  text-align: center;
  max-width: 584px;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  margin: 0 auto;
  padding-top: 160px;
  font-size: 32px;
  font-weight: 400;
  display: flex;
}

.menu {
  background-color: var(--black-color);
  height: 60px;
  justify-content: space-between;
  align-items: center;
  display: flex;
}

.link {
  text-decoration: none;
}

.list {
  list-style: none;
}

.nav {
  flex-grow: 1;
  justify-content: space-between;
  align-items: center;
  padding: 0 32px;
  display: flex;
}

.nav-list {
  flex-flow: row;
  gap: 40px;
  margin: 0 auto;
  padding: 8px 0;
  display: flex;
}

.cinemania {
  color: var(--white-color);
  text-align: center;
  font-family: Roboto;
  font-size: 24px;
  font-weight: 400;
  line-height: 28.13px;
}

.logo {
  z-index: 5;
  align-items: center;
  gap: 8px;
  display: flex;
}

.navigation {
  color: var(--grey-color);
  font-family: Roboto, sans-serif;
  font-size: 14px;
  font-weight: 500;
}

.navigation:active {
  color: var(--orange-color);
}

.nav-mobile {
  width: 204px;
  height: 100%;
  background-color: var(--black-color);
  border-radius: 0 8px 8px 0;
  transition-duration: .5s;
  position: absolute;
  top: 0;
  transform: translate(-100%);
}

.nav-list-mobile {
  flex-direction: column;
  gap: 24px;
  margin-top: 88px;
  margin-left: 20px;
  display: flex;
}

.text-mobile {
  color: var(--white-color);
  font-weight: 400;
}

.navigation-activ {
  color: var(--orange-color);
}

.button-menu {
  text-align: center;
  color: var(--grey-color);
  cursor: pointer;
  z-index: 5;
  background-color: rgba(0, 0, 0, 0);
  border: none;
  font-family: Roboto, sans-serif;
  font-size: 12px;
  font-weight: 500;
  line-height: 14.06px;
  display: none;
}

.backdrop {
  width: 100%;
  height: 100%;
  background-color: rgba(255, 255, 255, .2);
  transition-duration: .5s;
  position: fixed;
  top: 0;
  transform: translate(-100%);
}

.hidden {
  transform: translate(0%);
}

@media screen and (max-width: 767px) {
  .button-menu, .nav-mobile {
    display: flex;
  }

  .cinemania, .nav-list {
    display: none;
  }

  .mobile-menu {
    justify-content: space-between;
    align-items: center;
    display: flex;
  }

  .menu {
    height: 48px;
  }

  .logo {
    width: 46px;
  }
}

.myLibrary {
  position: relative;
}

.background {
  width: 100%;
  height: 100vh;
  background-color: rgba(0, 0, 0, 0);
  background-image: linear-gradient(79.39deg, #111 32.37%, rgba(17, 17, 17, 0) 72.02%), url("../Library-desktop.35c68c2c.png");
  background-position: 100%;
  background-repeat: repeat, no-repeat;
  background-size: cover;
  background-attachment: scroll, scroll;
  background-origin: padding-box, padding-box;
  background-clip: border-box, border-box;
  display: flex;
}

